.float-icon {
  width: auto;
  right: 0;
  position: fixed;
  bottom: 100px;
  display: flex;
  flex-direction: column;

  a {
    width: 50px;
    height: 50px;
    margin-bottom: 2px;
    padding: 0 8px;
    text-decoration: none;
    text-align: center;
    align-content: center;
    font-size: 13px;
    font-weight: 600;
    cursor: pointer;
    &:nth-child(odd) {
      background-color: #feda00;
      color: #012970;
    }
    &:nth-child(even),
    &:last-child {
      background-color: #3c4b64;
      color: white;
    }
  }
}
